













































import { Component, Watch, Vue } from 'vue-property-decorator';

@Component
export default class SideBar extends Vue {
  $router;
  private routes: Array<Object>=[];
  private default_active: string = '';
  private all_menu:any=[
    // {
    //   name: 'statistical',
    //   label: '数据统计',
    //   children: [
    //     {
    //       name: "resource_statistical_list",
    //       label: "资源统计"
    //     },
    //   ]
    // },
    {
      name: "az_list",
      label: "可用区管理"
    },
    {
      name: "pod_list",
      label: "POD管理"
    },
    {
      name: 'productType',
      label: '物理机产品类型管理',
    },
    {
      name: "physical_list",
      label: "物理资源管理"
    },
    {
      name: "instance_list",
      label: "云服务器管理"
    },
    {
      name: 'inventory',
      label: '库存',
      children: [
        {
          name: "inventory_list",
          label: "资源库存"
        },
        {
          name: "product_inventory_list",
          label: "商品库存"
        },
        {
          name: "vgpu_list",
          label: "vGPU授权库存",
        }

      ]
    },
    {
      name: 'cloud_management',
      label: '块存储管理',
      children: [
        {
          name: 'disk_inventory',
          label: '云盘库存'
        },
        {
          name: 'disk_sales_rate',
          label: '块存储设置'
        }
      ]
    },
    {
      name: "nas_inventory_list",
      label: "NAS库存",
    }
  ]
  private menu: Array<object> = [];
    private active_name: string = ''
  private FnChangeActive(name) {
    if (this.$route.name === name) {
      this.$router.go()
    } else {
      this.$router.push({name: name})
    }
  }
  private FnChangeUrl(name): void {
    if(this.active_name == name) {
      this.$router.go(0)
    } else {
      this.$router.push({ name: name })
    }
  };
  
  private created() {   
    this.routes = this.$router.options.routes.filter(item => !item.meta?.hidden);
    this.all_menu.forEach(item => {
      if (item.children) {
        let child_list = []
        item.children.forEach(child => {
          if (this.$store.state.auth_info[child.name]) {
            child_list.push(child)
          }
        })
        if (child_list.length > 0) {
          this.menu.push({
            name: item.name,
            label: item.label,
            children: child_list
          })
        }
      } else {
        if (this.$store.state.auth_info[item.name]) {
          this.menu.push(item)
        }
      }
    })
    // this.routes = this.$router.options.routes.filter(item => !item.meta?.hidden);
    // this.FnWatchRouter(this.$route)
  }

  @Watch('$route')
  private FnWatchRouter(to) {
    this.active_name = to.name;
    this.default_active = to.meta.menu || to.name;
  }
} 
